  -webkit-background-size: 240px 20px;
  content: "";
  vertical-align: top;
  background-image: url("leaflet.routing.icons.d26ffcf5.png");
  background-repeat: no-repeat;
  background-size: 240px 20px;
  width: 20px;
  height: 20px;
  margin: 0;
