  vertical-align: middle;
  cursor: pointer;
  background-color: #0000;
  display: inline-block;
}

.leaflet-routing-remove-waypoint:after {
  z-index: 1;
  content: "×";
  text-align: center;
  cursor: pointer;
  color: #ccc;
  background: #fff;
  width: 15px;
  height: 1px;
  margin: -16px auto auto;
  padding: 2px 4px 16px 2px;
